
import { defineComponent, computed, watch, ref } from 'vue'
import checkTable from '../../purchase_apply/search/cpn/checkTable.vue'
import { selectors } from './config/data'
import { useStore } from 'vuex'
export default defineComponent({
  name: '',
  setup() {
    const store = useStore()
    const purchaseId = ref('')
    watch(
      purchaseId,
      (id) => {
        store.commit('purchaseApply/setPurchaseId', id)
      },
      {
        deep: true
      }
    )
    let selector = computed(() => store.state.purchaseApply.selector)
    const changeSelector = (item: string) => {
      store.commit('purchaseApply/setSelector', item)
    }
    const searchByCondition = () => {
      store.dispatch('purchaseApply/getPurchaseplanListToAudit', {
        current: 0,
        size: 5
      })
    }
    searchByCondition()
    return {
      selector,
      changeSelector,
      selectors,
      purchaseId,
      searchByCondition
    }
  },
  components: {
    checkTable
  }
})
